* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  scroll-behavior: smooth;
}

/* Welcome Section */
.welcome-section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #f0f0f0;
  position: relative;
}

.welcome-section h1 {
  font-size: 2.5rem;
}

.welcome-section .name span {
  display: inline-block;
  opacity: 0;
  transform: translateY(-50px);
  animation: dropIn 0.5s ease forwards;
}

.welcome-section .name span:nth-child(1) {
  animation-delay: 0.1s;
}

.welcome-section .name span:nth-child(2) {
  animation-delay: 0.2s;
}

.welcome-section .name span:nth-child(3) {
  animation-delay: 0.3s;
}

.welcome-section .name span:nth-child(4) {
  animation-delay: 0.4s;
}

.welcome-section .name span:nth-child(5) {
  animation-delay: 0.5s;
}

.welcome-section .name span:nth-child(6) {
  animation-delay: 0.6s;
}

.welcome-section .name span:nth-child(7) {
  animation-delay: 0.7s;
}

.welcome-section .name span:nth-child(8) {
  animation-delay: 0.8s;
}

.welcome-section .name span:nth-child(9) {
  animation-delay: 0.9s;
}

.welcome-section .name span:nth-child(10) {
  animation-delay: 1.0s;
}

.welcome-section .name span:nth-child(11) {
  animation-delay: 1.1s;
}

.welcome-section .name span:nth-child(12) {
  animation-delay: 1.2s;
}

@keyframes dropIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Sections */
section {
  height: 100vh;
  padding: 50px;
  background: #ffffff;
  border-bottom: 1px solid #ddd;
}

section:nth-child(even) {
  background: #f9f9f9;
}

h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Sidebar Progress Bar */
.progress-bar {
  position: fixed;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 5px;
  height: 70%;
  background: #ddd;
}

.progress-bar::after {
  content: '';
  display: block;
  width: 100%;
  height: 0;
  background: #007acc;
  position: absolute;
  top: 0;
  transition: height 0.2s ease;
}